// src/shared/providers.js
// Provider implementations (Gemini + OpenAI) and shared request orchestration.
//
// Requirements implemented:
// - Gemini 3 Flash (via Google Generative Language API)
// - OpenAI GPT-5 Mini (via OpenAI Responses API)
// - No temperature / sampling parameters are ever sent
// - Provider selection happens via taskpane default provider (formulas cannot override)
// - AI.WEB uses provider-native web search mechanisms:
//   - Gemini: google_search tool
//   - OpenAI: web_search tool

import {
  PROVIDERS,
  DEFAULTS,
  normalizeProvider,
  getDefaultProvider,
  getApiKey,
  getModel,
  getGeminiReasoningEffort,
  getOpenAIReasoningEffort,
  getMaxOutputTokens,
  getConcurrencyLimit,
  clampInt,
  nowMs,
  sleep,
  LRUCache,
  hashKey,
  logRequest,
  setRuntimeStatus,
  getCacheClearAt,
  getPersistentCache,
  setPersistentCache,
  clearPersistentCache,
  redactSensitiveText
} from "./core.js";

// ---- In-memory cache (per runtime) ----
const CACHE = new LRUCache(800);
let _cacheClearLocalMs = 0;

async function syncCacheClearMarker() {
  const clearedAt = await getCacheClearAt();
  if (clearedAt > _cacheClearLocalMs) {
    _cacheClearLocalMs = clearedAt;
    CACHE.clear();
  }
}

// Deduplicate in-flight identical requests to avoid bursts (per runtime)
const INFLIGHT = new Map(); // key => Promise<AIResult>

const GEMINI_503_RETRY_DELAY_MS = 10000;
const GEMINI_503_MAX_RETRIES = 2;

// Concurrency limiter (per runtime)
const QUEUE_CANCELLED = Symbol("queue_cancelled");
const QUEUE_CANCELLED_RESULT = { ok: false, code: "QUEUE_CANCELLED", message: "File d'attente arrte." };
const CONCURRENCY_CACHE_TTL_MS = 1500;
let concurrencyLimitCached = DEFAULTS.concurrencyLimit;
let concurrencyLimitCachedAt = 0;

let active = 0;
const queue = [];
let statusTimer = null;
let pendingStatus = null;
let lastStatusKey = "";

function snapshotQueueStatus() {
  return { active, queued: queue.length };
}

function scheduleRuntimeStatusUpdate() {
  const snapshot = snapshotQueueStatus();
  pendingStatus = snapshot;
  if (statusTimer) return;
  statusTimer = setTimeout(() => {
    statusTimer = null;
    const status = pendingStatus;
    pendingStatus = null;
    if (!status) return;
    const key = `${status.active}:${status.queued}`;
    if (key === lastStatusKey) return;
    lastStatusKey = key;
    void setRuntimeStatus({ ...status, ts: nowMs() });
  }, 120);
}

async function getConcurrencyLimitCached() {
  const now = nowMs();
  if (now - concurrencyLimitCachedAt < CONCURRENCY_CACHE_TTL_MS) return concurrencyLimitCached;
  const value = await getConcurrencyLimit();
  concurrencyLimitCached = value;
  concurrencyLimitCachedAt = now;
  return value;
}

export function stopQueuedRequests() {
  if (!queue.length) return 0;
  const pending = queue.splice(0, queue.length);
  for (const resolve of pending) {
    resolve(QUEUE_CANCELLED);
  }
  scheduleRuntimeStatusUpdate();
  return pending.length;
}

async function withConcurrencyLimit(fn) {
  while (true) {
    const limit = await getConcurrencyLimitCached();
    if (active < limit) break;
    const gate = await new Promise((resolve) => {
      queue.push(resolve);
      scheduleRuntimeStatusUpdate();
    });
    if (gate === QUEUE_CANCELLED) return { ...QUEUE_CANCELLED_RESULT };
  }
  active++;
  scheduleRuntimeStatusUpdate();
  try {
    return await fn();
  } finally {
    active--;
    const next = queue.shift();
    if (next) next();
    scheduleRuntimeStatusUpdate();
  }
}

// Reset runtime status on load to avoid stale UI.
void setRuntimeStatus({ active: 0, queued: 0, ts: nowMs() });

function isNonEmptyString(s) {
  return typeof s === "string" && s.trim().length > 0;
}

function normalizeBoolean(v, def = false) {
  if (typeof v === "boolean") return v;
  if (typeof v === "number") return v !== 0;
  if (typeof v === "string") {
    const s = v.trim().toLowerCase();
    if (["1", "true", "yes", "y", "on"].includes(s)) return true;
    if (["0", "false", "no", "n", "off"].includes(s)) return false;
  }
  return def;
}

function normalizeTimeoutMs(v) {
  return clampInt(v, 1000, 120000, DEFAULTS.timeoutMs);
}

function normalizeRetry(v) {
  return clampInt(v, 0, 5, DEFAULTS.retry);
}

function normalizeMaxOutputTokens(v) {
  return clampInt(v, 1, 128000, DEFAULTS.maxOutputTokens);
}

function toLowerMsg(v) {
  return String(v || "").toLowerCase();
}

function looksLikeStructuredOutputUnsupported(message) {
  const s = toLowerMsg(message);
  if (!s) return false;
  return (
    s.includes("responseschema") ||
    s.includes("response schema") ||
    s.includes("response_schema") ||
    s.includes("responsemime") ||
    s.includes("response mime") ||
    s.includes("response_mime") ||
    s.includes("response_mime_type") ||
    s.includes("json_schema") ||
    s.includes("response_format") ||
    s.includes("text.format")
  );
}

function looksLikeWebSearchUnsupported(message) {
  const s = toLowerMsg(message);
  if (!s) return false;
  if (!(s.includes("web_search") || s.includes("google_search") || s.includes("web search") || s.includes("google search"))) {
    return false;
  }
  return (
    s.includes("not supported") ||
    s.includes("unsupported") ||
    s.includes("unknown") ||
    s.includes("unrecognized") ||
    s.includes("invalid") ||
    s.includes("not enabled") ||
    s.includes("not available")
  );
}

function isGeminiServiceUnavailable(provider, result) {
  return provider === PROVIDERS.GEMINI && !result?.ok && result?.status === 503;
}

function ensureJsonInstruction(system) {
  const suffix = " Return valid JSON only.";
  if (!isNonEmptyString(system)) return "Return valid JSON only.";
  const s = String(system).trim();
  if (/json/i.test(s)) return s;
  return s + suffix;
}

function extractFormulaFromText(text) {
  if (!isNonEmptyString(text)) return "";
  const raw = String(text).trim();
  const match = raw.match(/=[^\n\r]+/);
  const formula = (match ? match[0] : raw).trim();
  if (!formula.startsWith("=")) return "";
  return formula;
}

function getFormulaForLog(fnName, text) {
  if (fnName !== "AI.FORMULA") return "";
  return extractFormulaFromText(text);
}

function resolveGeminiThinkingConfig(model, effort) {
  const level = String(effort || "").trim().toLowerCase();
  if (!level || level === "auto" || level === "none") return null;

  const modelId = String(model || "").trim().toLowerCase();
  if (!modelId.includes("gemini")) return null;

  if (modelId.includes("gemini-3")) {
    if (modelId.includes("flash")) {
      const allowed = new Set(["minimal", "low", "medium", "high"]);
      const normalized = allowed.has(level) ? level : "low";
      return { thinkingLevel: normalized.toUpperCase() };
    }
    const normalized = level === "high" || level === "medium" ? "high" : "low";
    return { thinkingLevel: normalized.toUpperCase() };
  }

  if (modelId.includes("gemini-2.5")) {
    const budgets = {
      minimal: 512,
      low: 2048,
      medium: 8192,
      high: 24576
    };
    let budget = budgets[level];
    if (!budget) return null;
    if (modelId.includes("flash-lite")) budget = Math.max(512, budget);
    if (modelId.includes("pro")) budget = Math.max(128, budget);
    return { thinkingBudget: budget };
  }

  return null;
}

// ---- Public API ----

/**
 * @typedef {Object} AIGenerateRequest
 * @property {string=} provider Optional override (should only be used by taskpane tests, not formulas).
 * @property {string=} system System instruction.
 * @property {string} user User prompt.
 * @property {boolean=} webSearch Enable provider web search tool.
 * @property {number=} maxOutputTokens Override max tokens (optional; normally from panel).
 * @property {number=} timeoutMs
 * @property {number=} retry
 * @property {boolean=} cache
 * @property {number=} cacheTtlSec
 * @property {string=} responseMimeType e.g., "application/json"
 * @property {Object=} responseJsonSchema JSON schema (strict) for structured output.
 * @property {string=} functionName Name for diagnostics.
 * @property {string=} functionCall Excel-style function call for diagnostics.
 */

/**
 * @typedef {Object} AIResult
 * @property {boolean} ok
 * @property {string=} text
 * @property {Array<{title?: string, url: string}>=} sources
 * @property {string=} provider
 * @property {string=} model
 * @property {Object=} usage
 * @property {number=} status
 * @property {string=} code
 * @property {string=} message
 */

export async function aiGenerate(req) {
  const start = nowMs();
  const provider = normalizeProvider(req?.provider) || (await getDefaultProvider());
  const model = await getModel(provider);
  const apiKey = await getApiKey(provider);
  const fnName = req?.functionName || "aiGenerate";
  const functionCall = isNonEmptyString(req?.functionCall) ? String(req.functionCall) : "";
  const reasoningEffort = provider === PROVIDERS.OPENAI ? await getOpenAIReasoningEffort() : "";
  const geminiReasoningEffort = provider === PROVIDERS.GEMINI ? await getGeminiReasoningEffort() : "";
  const reasoningEffortLabel = provider === PROVIDERS.OPENAI ? reasoningEffort : geminiReasoningEffort;

  if (!apiKey) {
    logRequest({
      fn: fnName,
      provider,
      model,
      ok: false,
      ms: nowMs() - start,
      cache: false,
      reasoningEffort: reasoningEffortLabel || undefined,
      call: functionCall || undefined,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    });
    return {
      ok: false,
      provider,
      model,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    };
  }

  const user = String(req?.user || "");
  const system = isNonEmptyString(req?.system) ? String(req.system) : "";

  const webSearch = normalizeBoolean(req?.webSearch, false);
  const timeoutMs = normalizeTimeoutMs(req?.timeoutMs);
  const retry = normalizeRetry(req?.retry);
  const gemini503RetryMax = Math.min(retry, GEMINI_503_MAX_RETRIES);

  const cfgMax = await getMaxOutputTokens();
  const maxOutputTokens = normalizeMaxOutputTokens(req?.maxOutputTokens ?? cfgMax);
  const thinkingConfig = provider === PROVIDERS.GEMINI ? resolveGeminiThinkingConfig(model, geminiReasoningEffort) : null;

  const cacheEnabled = normalizeBoolean(req?.cache, DEFAULTS.cache);
  const ttlSec = clampInt(
    req?.cacheTtlSec ?? (webSearch ? DEFAULTS.webCacheTtlSec : DEFAULTS.cacheTtlSec),
    1,
    365 * 24 * 3600,
    webSearch ? DEFAULTS.webCacheTtlSec : DEFAULTS.cacheTtlSec
  );

  await syncCacheClearMarker();

  // Cache key (no temperature anywhere)
  const cacheKeyPayload = {
    v: 3,
    provider,
    model,
    maxOutputTokens,
    reasoningEffort,
    geminiReasoningEffort,
    webSearch,
    system,
    user,
    responseMimeType: req?.responseMimeType || "",
    responseJsonSchema: req?.responseJsonSchema || null
  };

  const cacheKey = await hashKey(cacheKeyPayload);

  if (cacheEnabled) {
    const cached = CACHE.get(cacheKey);
    if (cached) {
      const formula = getFormulaForLog(fnName, cached?.text);
      logRequest({
        fn: fnName,
        provider,
        model,
        ok: true,
        ms: nowMs() - start,
        cache: true,
        cacheKind: "memory",
        reasoningEffort: reasoningEffortLabel || undefined,
        call: functionCall || undefined,
        inputTokens: 0,
        outputTokens: 0,
        reasoningTokens: 0,
        totalTokens: 0,
        formula: formula || undefined
      });
      return { ...cached, cached: true, cacheKind: "memory" };
    }
    const persisted = await getPersistentCache(cacheKey);
    if (persisted) {
      const formula = getFormulaForLog(fnName, persisted?.text);
      CACHE.set(cacheKey, persisted, ttlSec * 1000);
      logRequest({
        fn: fnName,
        provider,
        model,
        ok: true,
        ms: nowMs() - start,
        cache: true,
        cacheKind: "persistent",
        reasoningEffort: reasoningEffortLabel || undefined,
        call: functionCall || undefined,
        inputTokens: 0,
        outputTokens: 0,
        reasoningTokens: 0,
        totalTokens: 0,
        formula: formula || undefined
      });
      return { ...persisted, cached: true, cacheKind: "persistent" };
    }
  }

  if (INFLIGHT.has(cacheKey)) return INFLIGHT.get(cacheKey);

  const p = withConcurrencyLimit(async () => {
    let attempt = 0;
    let lastErr = null;

    const callProvider = async (opts) =>
      provider === PROVIDERS.OPENAI ? callOpenAI(opts) : callGemini(opts);

    while (attempt <= retry) {
      attempt++;
      try {
        const baseOpts = {
          apiKey,
          model,
          system,
          user,
          maxOutputTokens,
          reasoningEffort,
          thinkingConfig,
          timeoutMs,
          webSearch,
          responseMimeType: req?.responseMimeType,
          responseJsonSchema: req?.responseJsonSchema
        };

        let result = await callProvider(baseOpts);

        if (!result.ok && !isGeminiServiceUnavailable(provider, result)) {
          let opts = baseOpts;

          // Fallback #1: if web search fails, retry without tools.
          if (opts.webSearch) {
            const shouldRetryNoWeb =
              looksLikeWebSearchUnsupported(result.message) || result.code === "API_ERROR" || result.code === "EMPTY_RESPONSE";
            if (shouldRetryNoWeb) {
              opts = { ...opts, webSearch: false };
              result = await callProvider(opts);
            }
          }

          // Fallback #2: if structured output fails, retry without schema/responseMimeType.
          const wantsStructured = opts.responseMimeType || opts.responseJsonSchema;
          if (!result.ok && wantsStructured) {
            const shouldRetryPlainJson =
              looksLikeStructuredOutputUnsupported(result.message) ||
              result.code === "API_ERROR" ||
              result.code === "EMPTY_RESPONSE";
            if (shouldRetryPlainJson) {
              opts = {
                ...opts,
                responseMimeType: undefined,
                responseJsonSchema: undefined,
                system: ensureJsonInstruction(opts.system)
              };
              result = await callProvider(opts);
            }
          }
        }

        const shouldRetryServiceUnavailable =
          isGeminiServiceUnavailable(provider, result) && attempt <= gemini503RetryMax;

        // Log (best-effort)
        const usage = normalizeUsage(result?.usage);
        const formula = result.ok ? getFormulaForLog(fnName, result.text) : "";
        logRequest({
          fn: fnName,
          provider,
          model,
          ok: result.ok,
          ms: nowMs() - start,
          attempt,
          cache: false,
          reasoningEffort: reasoningEffortLabel || undefined,
          call: functionCall || undefined,
          inputTokens: usage.inputTokens,
          outputTokens: usage.outputTokens,
          reasoningTokens: usage.reasoningTokens,
          cachedInputTokens: usage.cachedInputTokens,
          totalTokens: usage.totalTokens,
          code: result.code,
          message: result.message,
          formula: formula || undefined
        });

        if (shouldRetryServiceUnavailable) {
          await sleep(GEMINI_503_RETRY_DELAY_MS);
          continue;
        }

        if (result.ok && cacheEnabled) {
          CACHE.set(cacheKey, result, ttlSec * 1000);
          await setPersistentCache(cacheKey, result, ttlSec * 1000, { provider, model });
        }

        return result;
      } catch (e) {
        lastErr = e;
        if (attempt <= retry) await sleep(250 * attempt);
      }
    }

    const msg = redactSensitiveText(lastErr?.message ? String(lastErr.message) : "Erreur inconnue.");
    logRequest({
      fn: fnName,
      provider,
      model,
      ok: false,
      ms: nowMs() - start,
      cache: false,
      reasoningEffort: reasoningEffortLabel || undefined,
      call: functionCall || undefined,
      code: "API_ERROR",
      message: msg
    });
    return { ok: false, provider, model, code: "API_ERROR", message: msg };
  });

  INFLIGHT.set(cacheKey, p);

  try {
    const result = await p;
    if (result?.code === "QUEUE_CANCELLED") {
      logRequest({
        fn: fnName,
        provider,
        model,
        ok: false,
        ms: nowMs() - start,
        cache: false,
        reasoningEffort: reasoningEffortLabel || undefined,
        call: functionCall || undefined,
        inputTokens: 0,
        outputTokens: 0,
        reasoningTokens: 0,
        cachedInputTokens: 0,
        totalTokens: 0,
        code: result.code,
        message: result.message
      });
    }
    return result;
  } finally {
    INFLIGHT.delete(cacheKey);
  }
}

/**
 * Lightweight connectivity test for a provider (used from taskpane and AI.TEST()).
 */
export async function aiMinimalTest(providerOverride) {
  const provider = normalizeProvider(providerOverride) || (await getDefaultProvider());
  const model = await getModel(provider);
  const apiKey = await getApiKey(provider);

  if (!apiKey) {
    return {
      ok: false,
      provider,
      model,
      code: "API_KEY_MISSING",
      message: `Cl API manquante pour le fournisseur ${provider}.`
    };
  }

  // Tiny request
  return await aiGenerate({
    provider,
    system: "You are a test endpoint. Reply with exactly: OK",
    user: "OK",
    maxOutputTokens: 500,
    cache: false,
    webSearch: false,
    functionName: "AI.MINIMAL_TEST"
  });
}

// ---- Provider: Gemini (Google Generative Language API) ----

function geminiEndpoint(model) {
  return `https://generativelanguage.googleapis.com/v1beta/models/${encodeURIComponent(model)}:generateContent`;
}

async function callGemini({
  apiKey,
  model,
  system,
  user,
  maxOutputTokens,
  thinkingConfig,
  timeoutMs,
  webSearch,
  responseMimeType,
  responseJsonSchema
}) {
  const url = geminiEndpoint(model);

  const body = {
    contents: [{ role: "user", parts: [{ text: user }] }],
    generationConfig: {
      maxOutputTokens
    }
  };

  if (thinkingConfig && typeof thinkingConfig === "object") {
    body.generationConfig.thinkingConfig = thinkingConfig;
  }

  if (isNonEmptyString(system)) {
    // API uses lowerCamelCase for this field in JSON representation
    // Force explicit JSON instruction for robustness
    const sysText = (responseMimeType === "application/json" || responseJsonSchema)
      ? system + " You must return valid JSON."
      : system;
    body.systemInstruction = { role: "system", parts: [{ text: sysText }] };
  }

  // Structured output (JSON) when requested
  if (responseMimeType) {
    body.generationConfig.responseMimeType = responseMimeType;
  }
  if (responseJsonSchema) {
    body.generationConfig.responseSchema = responseJsonSchema;
  }

  // Web grounding tool
  if (webSearch) {
    // For REST v1beta, the tool name is usually google_search (snake_case).
    // Note: Some newer endpoints might respect googleSearch, but snake_case is safer for REST.
    body.tools = [{ google_search: {} }];
  }

  const res = await fetchWithTimeout(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "x-goog-api-key": apiKey
    },
    body: JSON.stringify(body)
  }, timeoutMs);

  const json = await safeJson(res);

  if (!res.ok) {
    const msg = extractGeminiError(json) || `Gemini API error (${res.status}).`;
    return {
      ok: false,
      provider: PROVIDERS.GEMINI,
      model,
      status: res.status,
      code: "API_ERROR",
      message: redactSensitiveText(msg)
    };
  }

  const text = extractGeminiText(json);
  const sources = extractGeminiSources(json);
  const usage = extractGeminiUsage(json);

  if (!isNonEmptyString(text)) {
    return { ok: false, provider: PROVIDERS.GEMINI, model, code: "EMPTY_RESPONSE", message: "Rponse vide de Gemini." };
  }

  return { ok: true, provider: PROVIDERS.GEMINI, model, text: text.trim(), sources, usage };
}

function extractGeminiText(json) {
  try {
    const c = json?.candidates?.[0];
    const parts = c?.content?.parts;
    if (Array.isArray(parts)) {
      return parts.map((p) => p?.text || "").join("");
    }
    return "";
  } catch {
    return "";
  }
}

function extractGeminiError(json) {
  try {
    const msg = json?.error?.message;
    return msg ? String(msg) : "";
  } catch {
    return "";
  }
}

function extractGeminiSources(json) {
  try {
    const c = json?.candidates?.[0];
    const md = c?.groundingMetadata;
    const chunks = md?.groundingChunks;
    if (!Array.isArray(chunks)) return [];

    const out = [];
    for (const ch of chunks) {
      const web = ch?.web;
      if (!web?.uri) continue;
      out.push({ title: web?.title || "", url: web.uri });
    }

    return dedupeSources(out);
  } catch {
    return [];
  }
}

// ---- Provider: OpenAI (Responses API) ----

async function callOpenAI({
  apiKey,
  model,
  system,
  user,
  maxOutputTokens,
  reasoningEffort,
  timeoutMs,
  webSearch,
  responseMimeType,
  responseJsonSchema
}) {
  const url = "https://api.openai.com/v1/responses";

  const body = {
    model,
    input: user, // Responses API uses 'input' (string or array of objects)
    max_output_tokens: maxOutputTokens,
    store: false
  };

  if (isNonEmptyString(reasoningEffort)) {
    body.reasoning = { effort: reasoningEffort };
  }

  // System prompt
  if (isNonEmptyString(system)) {
    const sysText = (responseMimeType === "application/json" || responseJsonSchema)
      ? system + " You must return valid JSON."
      : system;
    body.instructions = sysText;
  }

  // Structured output (JSON)
  // - If a JSON schema is provided: use json_schema strict mode.
  // - Else if responseMimeType is JSON: request a json_object.
  if (responseJsonSchema) {
    // For GPT-5 Mini / v1/responses, we try to use the most standard way if possible.
    // If body.text format fails, we rely on the prompt.
    // We kept the previous implementation but added prompt reinforcement.
    body.text = {
      format: {
        type: "json_schema",
        name: "excel_ai_schema",
        strict: true,
        schema: responseJsonSchema
      }
    };
  } else if (responseMimeType === "application/json") {
    body.text = { format: { type: "json_object" } };
  }

  // Web search tool
  if (webSearch) {
    body.tools = [{ type: "web_search" }];
    body.include = ["web_search_call.action.sources"];
  }

  const res = await fetchWithTimeout(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${apiKey}`
    },
    body: JSON.stringify(body)
  }, timeoutMs);

  const json = await safeJson(res);

  if (!res.ok) {
    const msg = extractOpenAIError(json) || `OpenAI API error (${res.status}).`;
    return { ok: false, provider: PROVIDERS.OPENAI, model, code: "API_ERROR", message: redactSensitiveText(msg) };
  }

  const text = extractOpenAIText(json);
  const sources = webSearch ? extractOpenAISources(json) : [];
  const usage = extractOpenAIUsage(json);

  if (!isNonEmptyString(text)) {
    return {
      ok: false,
      provider: PROVIDERS.OPENAI,
      model,
      code: "EMPTY_RESPONSE",
      message: "Rponse vide d'OpenAI."
    };
  }

  return { ok: true, provider: PROVIDERS.OPENAI, model, text: text.trim(), sources, usage };
}

function extractOpenAIText(json) {
  // 1. Try direct output_text (common in simple Responses)
  try {
    if (typeof json?.output_text === "string" && json.output_text.trim()) {
      return json.output_text;
    }
  } catch {
    // ignore
  }

  // 2. Try iterating over 'output' items (complex/agentic responses)
  try {
    const out = json?.output;
    if (Array.isArray(out)) {
      const texts = [];
      for (const item of out) {
        if (typeof item?.text === "string" && item.text.trim()) texts.push(item.text);
        if (typeof item?.output_text === "string" && item.output_text.trim()) texts.push(item.output_text);
        if (item?.type === "output_text" && typeof item?.text === "string") texts.push(item.text);
        if (item?.type === "text" && typeof item?.text === "string") texts.push(item.text);

        // Standard message output
        if (item?.type === "message") {
          const content = item?.content;
          if (Array.isArray(content)) {
            for (const part of content) {
              if (part?.type === "text" && part?.text) texts.push(part.text);
              if (part?.type === "output_text" && part?.text) texts.push(part.text);
            }
          } else if (typeof content === "string") {
            texts.push(content);
          }
        }

        if (item?.type !== "message") {
          const content = item?.content;
          if (Array.isArray(content)) {
            for (const part of content) {
              if (part?.type === "text" && part?.text) texts.push(part.text);
              if (part?.type === "output_text" && part?.text) texts.push(part.text);
            }
          } else if (typeof content === "string") {
            texts.push(content);
          }
        }
      }
      if (texts.length > 0) return texts.join("\n").trim();
    }
  } catch {
    // ignore
  }

  // 3. Fallback: Chat Completions style (just in case model acts differently)
  try {
    return json?.choices?.[0]?.message?.content || "";
  } catch {
    return "";
  }
}

function extractOpenAIError(json) {
  try {
    const msg = json?.error?.message;
    return msg ? String(msg) : "";
  } catch {
    return "";
  }
}

function extractOpenAISources(json) {
  try {
    const out = json?.output;
    if (!Array.isArray(out)) return [];

    const sources = [];
    for (const item of out) {
      if (item?.type !== "web_search_call") continue;
      const s = item?.action?.sources;
      if (!Array.isArray(s)) continue;
      for (const src of s) {
        if (!src?.url) continue;
        sources.push({ title: src?.title || "", url: src.url });
      }
    }

    return dedupeSources(sources);
  } catch {
    return [];
  }
}

function extractGeminiUsage(json) {
  try {
    const u = json?.usageMetadata;
    if (!u) return null;
    return {
      inputTokens: toInt(u?.promptTokenCount),
      outputTokens: toInt(u?.candidatesTokenCount),
      reasoningTokens: toInt(u?.thoughtsTokenCount),
      totalTokens: toInt(u?.totalTokenCount),
      cachedInputTokens: toInt(u?.cachedContentTokenCount)
    };
  } catch {
    return null;
  }
}

function extractOpenAIUsage(json) {
  try {
    const u = json?.usage;
    if (!u) return null;
    const rawOutputTokens = toInt(u?.output_tokens);
    const reasoningTokens = toInt(u?.output_tokens_details?.reasoning_tokens);
    const outputTokens = Math.max(0, rawOutputTokens - reasoningTokens);
    return {
      inputTokens: toInt(u?.input_tokens),
      outputTokens,
      reasoningTokens,
      totalTokens: toInt(u?.total_tokens),
      cachedInputTokens: toInt(u?.input_tokens_details?.cached_tokens)
    };
  } catch {
    return null;
  }
}

function normalizeUsage(usage) {
  if (!usage || typeof usage !== "object") {
    return { inputTokens: 0, outputTokens: 0, reasoningTokens: 0, totalTokens: 0, cachedInputTokens: 0 };
  }
  const inputTokens = toInt(usage.inputTokens);
  const outputTokens = toInt(usage.outputTokens);
  const reasoningTokens = toInt(usage.reasoningTokens);
  const cachedInputTokens = toInt(usage.cachedInputTokens);
  const reportedTotal = toInt(usage.totalTokens);
  const computedTotal = inputTokens + outputTokens + reasoningTokens;
  const totalTokens = Math.max(reportedTotal, computedTotal);
  return {
    inputTokens,
    outputTokens,
    reasoningTokens,
    totalTokens,
    cachedInputTokens
  };
}

function toInt(value) {
  const n = Number(value);
  if (!Number.isFinite(n) || n <= 0) return 0;
  return Math.floor(n);
}

export async function clearAllCaches() {
  CACHE.clear();
  await clearPersistentCache();
}

function dedupeSources(list) {
  const seen = new Set();
  const out = [];
  for (const s of list || []) {
    const url = String(s?.url || "").trim();
    if (!url) continue;
    const key = url.toLowerCase();
    if (seen.has(key)) continue;
    seen.add(key);
    out.push({ title: String(s?.title || "").trim(), url });
  }
  return out;
}

// ---- Helpers: fetch + timeout + JSON parsing ----

async function fetchWithTimeout(url, options, timeoutMs) {
  const t = normalizeTimeoutMs(timeoutMs);

  if (typeof AbortController === "undefined") {
    // Basic fetch without cancellation (should be rare in modern Office runtimes)
    return await fetch(url, options);
  }

  const ctrl = new AbortController();
  const id = setTimeout(() => ctrl.abort(), t);

  try {
    return await fetch(url, { ...options, signal: ctrl.signal });
  } finally {
    clearTimeout(id);
  }
}

async function safeJson(res) {
  try {
    return await res.json();
  } catch {
    return {};
  }
}
